.box {
  width: 300px;
  height: 200px;
  margin: 0 auto;
  .move {
    display: block;
    float: left;
    width: 100px;
    height: 60px;
    text-decoration: none;
    color: black;
    border: 1px solid #000;
    text-align: center;
    margin: 10px;
    padding-top: 15px;
    box-sizing: border-box;
  }
}
